drop-label div.layout-row > label {
  color: #7b8082;
}
drop-label div.layout-row > label.required:before {
  content: '*';
  color: red;
  margin: 3px;
  vertical-align: middle;
}
drop-label input {
  margin: 8px 6px;
  padding: 0 3px;
}
drop-label md-input-container {
  padding: 0;
  margin: 2px 5px;
}
drop-label md-input-container.md-input-has-value .md-select-value > span:not(.md-select-icon) {
  transform: none;
}



