input-label-drop div.layout-row > label {
  line-height: 42px;
  color: #7b8082;
}
input-label-drop div.layout-row > label.required:before {
  content: '*';
  color: red;
  margin: 3px;
  vertical-align: middle;
}
input-label-drop .input-container {
  margin: 6px;
}
input-label-drop .input-container input {
  padding: 0 3px;
  margin: 2px 0px;
  align-self: inherit;
}
input-label-drop .input-container md-input-container {
  margin: 0 0 0 2px;
}



