input-label div.layout-row > label {
  line-height: 42px;
  color: #7b8082;
}
input-label div.layout-row > label.required:before {
  content: '*';
  color: red;
  margin: 3px;
  vertical-align: middle;
}
input-label textarea,
input-label input {
  margin: 8px 6px;
  padding: 0 3px;
}
input-label textarea {
  color: #fff3dd;
  background-color: #3b4145;
  border-color: transparent;
  width: 100%;
  height: 60px;
  resize: none;
  border-radius: 2px;
}
input-label textarea:focus {
  outline: transparent auto 5px;
}



