input-file-select div.layout-row > label {
  color: #7b8082;
}
input-file-select div.layout-row > label.required:before {
  content: '*';
  color: red;
  margin: 3px;
  vertical-align: middle;
}
input-file-select .inputContainer {
  margin: 0 6px;
}
input-file-select #file {
  opacity: 0;
  position: absolute;
}
input-file-select #file_1 {
  width: 100%;
  box-sizing: border-box;
}



